import { Injectable } from '@angular/core';
import { IPlainObject, RxCommandFactoryService } from '@helix/platform/shared/api';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class RxProcessInstanceCommandsService {
  private startProcessCommand = this.rxCommandFactoryService.forResourceType(
    'com.bmc.arsys.rx.application.process.command.StartProcessInstanceCommand'
  );

  constructor(private rxCommandFactoryService: RxCommandFactoryService) {}

  start(processDefinitionName: string, processInputValues: IPlainObject): Observable<any> {
    return this.startProcessCommand.execute(
      {
        processDefinitionName,
        processInputValues
      },
      {
        observe: 'response',
        responseType: 'text'
      }
    );
  }
}
